@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Josefin Sans', sans-serif;
  font-size: bold;
}



.main_div {

  width: 100%;

  height: 100vh;

  background: #08a5aa;

  display: flex;

  flex-direction: row;

  justify-content: center;

  align-items: center;

  text-align: center;

}



.center_div {

  width: 25%;

  height: 70vh;

  background-color: #f4f4f4;

  box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);

  border-radius: 15px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;

}



h1 {

  color: rgba(0, 0, 0, 0.87);

  font-size: 50px;

  margin-bottom: 30px;

  min-width: 64px;

  box-sizing: border-box;

  font-family: "Comfortaa", cursive;

  /* plz subscibe to thapatechnical channel */

  font-weight: 500;

  border-radius: 4px;

  letter-spacing: 0.02857em;

  text-transform: uppercase;

  text-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);

}











input {

  text-align: center;

  height: 30px;

  border: none;

  background: transparent;

  font-size: 20px;

  font-weight: bold;

  width: 73%;

  border-bottom: 2px solid #8566aa;

  outline: none;

  margin-right: 20px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
  padding: 20px;
  border-radius: 5px;
}



.newBtn {

  width: 50px;

  height: 55px;

  background-color: #16a085 !important;

  color: white !important;

  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);

  /* plz subscibe to thapatechnical channel */

  border-radius: 50% !important;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;

}







.newBtn:hover {



  color: #16a085 !important;



  background: white !important;



}





/* //the add symbol inside the button in app comp */



.MuiSvgIcon-root {



  width: 2rem !important;



  height: 2rem !important;



}





ol {


  
  margin: 0 10px;
  margin-top: 20px;
  padding: 5px 0;



  /* margin-right: 50px; */
  /* border: 2px solid red; */
  height: 323px;
  overflow-y: scroll;
  scroll-behavior: smooth;

  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;

}





ol li {



  padding-left: 0px;



  text-align: left;



  font-size: 20px;



  font-weight: bold;



  min-height: 40px;



  display: flex;



  align-items: center;



  color: #8566aa;



  text-transform: capitalize;



  /* plz subscibe to thapatechnical channel */



  list-style: none;



}





.todo_style {



  display: flex;



  flex-direction: row;



  justify-content: left;



  align-items: center;



}





.todo_style .deleteIcon {



  width: 25px;



  height: 25px;



  display: flex;



  justify-content: center;



  align-items: center;



  background-color: #8566aa;



  border-radius: 50%;



  margin: 0 15px 0 35px;



  padding: 5px;



  color: aliceblue;



  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);



}





.todo_style .deleteIcon:hover {



  background-color: transparent;



  color: crimson;



  box-shadow: 5px 5px 15px -5px rgba(255, 0, 0, 0.5);



}





.todo_style .fa-times:hover .list_style {



  color: crimson;



}





@media (max-width: 768px) {



  .center_div {



    width: 55%;



  }





  .todo_style .fa-times {



    margin: 0 15px 0 15px;



    display: flex;



    justify-content: center;



    align-items: center;



    /* plz subscibe to thapatechnical channel */



  }





  button {



    margin-left: 5px;



    min-height: 40px;



    width: 40px;



    color: #fff;



    font-size: 30px;



  }



}

